import { Component, Element, Event, EventEmitter, Fragment, h, Prop, State } from '@stencil/core';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import { ClickElementTypes, ElementTypes, EventActions } from '../../utility/constants';
import analyticsManager from '../../services/analytics/analytics-manager';
import { navContentStore } from '../../state/nav-content-store';
import { isDesktop, isString } from '../../utility/helper';
import { getHeadlineText, getSubtext } from '../../services/menu-items-service';
import { findTopLevelRoutingKey } from '../../entities/routing-key';
import { componentsOnReady } from '../../utility/ready';
import { Timeout } from '../../utility/timeout';

@Component({
  tag: 'phn-drawer-header',
  styleUrl: 'drawer-header.scss',
  scoped: true
})
export class DrawerHeader {
  @Prop() backButtonText = '';
  @Prop() backButtonAnalyticsId!: string;
  @Prop() showBackButton = false;
  @Prop() activeRoutingKey!: string;
  @Prop() breakpoint!: number;
  @Prop() receivedContent = false;
  @Prop() locale?: string;

  @State() showSkeleton = true;

  @Event() clickDrawerHeaderBack!: EventEmitter<void>;
  @Event() clickDrawerHeaderClose!: EventEmitter<void>;

  @Element() el!: HTMLPhnDrawerHeaderElement;

  componentWillLoad(): void {
    this.waitForComponentsToLoad();
  }

  private async waitForComponentsToLoad() {
    await componentsOnReady(this.el, new Timeout(5000));
    this.showSkeleton = !this.receivedContent;
  }

  private sendDataLayerEvent() {
    globalDataLayerService.pushDataLayerEvent(EventActions.MENU_CLOSE_CLICK, {
      componentClick: {
        clickElementId: 'mainmenu.drawer.close',
        clickElementName: 'X',
        clickElementType: ClickElementTypes.Button,
        targetElement: 'level-0'
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          this.activeRoutingKey,
          ElementTypes.closeButton
        ),
        displayElementName: 'X'
      }
    });
  }

  render(): HTMLPhnDrawerHeaderElement {
    const topLevelRoutingKey = findTopLevelRoutingKey(this.activeRoutingKey);
    const isMobile = !isDesktop(this.breakpoint);
    const displayHeadlineContent =
      topLevelRoutingKey !== undefined
        ? getHeadlineText(navContentStore.state, this.activeRoutingKey, topLevelRoutingKey)
        : '';

    const modelType = displayHeadlineContent.includes('mainmenu.') ? displayHeadlineContent.split('mainmenu.')[1] : '';
    const subtext = getSubtext(topLevelRoutingKey, this.activeRoutingKey, this.locale);
    return (
      <div class="drawer-header">
        {this.showBackButton ? (
          <Fragment>
            <phn-back-button
              class="back-button"
              onClick={() => {
                this.clickDrawerHeaderBack.emit();
              }}
              analyticsId={this.backButtonAnalyticsId}
              text={isMobile ? '' : this.backButtonText}
              activeRoutingKey={this.activeRoutingKey}
              style={!this.showSkeleton ? {} : { display: 'none' }}
            />
            {this.showSkeleton ? <div class="button-skeleton" /> : null}
          </Fragment>
        ) : (
          <div class="spacer" />
        )}

        {isMobile ? (
          modelType === '' ? (
            <Fragment>
              {this.showSkeleton && this.showBackButton ? <div class="skeleton-headline"></div> : null}
              <phn-heading
                text={displayHeadlineContent}
                style={this.showSkeleton || !isString(displayHeadlineContent) ? { display: 'none' } : {}}
                class="heading-items"
                subtext={subtext}
              />
            </Fragment>
          ) : (
            <Fragment>
              {this.showSkeleton ? <div class="skeleton-headline"></div> : null}
              <phn-signature-heading text={modelType} subtext={subtext} />
            </Fragment>
          )
        ) : null}

        <Fragment>
          <phn-p-button-pure
            class="close-button"
            icon="close"
            hideLabel="true"
            aria={{ 'aria-label': 'close dialog' }}
            size="small"
            style={!this.showSkeleton ? {} : { display: 'none' }}
            data-test-id="close-button"
            onClick={() => {
              this.sendDataLayerEvent();
              this.clickDrawerHeaderClose.emit();
            }}
          >
            Close Dialog
          </phn-p-button-pure>
          {this.showSkeleton ? <div class="button-skeleton" /> : null}
        </Fragment>
      </div>
    );
  }
}
