@import '../../scss';

:host {
  display: block;
}

.drawer-header {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.heading-items {
  margin-bottom: p-px-to-rem(4px);
  align-items: center;
}

.skeleton-headline {
  width: p-px-to-rem(150px);
  height: p-px-to-rem(24px);
  background: $pds-theme-light-contrast-low;
  border-radius: 4px;
}

.button-skeleton {
  margin-left: p-px-to-rem(8px);
  border-radius: p-px-to-rem(4px);
  height: p-px-to-rem(24px);
  width: p-px-to-rem(24px);
  background: $pds-theme-light-contrast-low;
}
